import React from "react";
import "./Portada.scss";
import { Row } from "react-bootstrap";

const Portada = () => {
  return (
    <Row className="row_portada">
      <img
        src="https://i.imgur.com/0PuEKhc.png"
        alt=""
        className="img_portada"
      />
      <h1 className="h1_prensa">Prensa</h1>
    </Row>
  );
};

export default Portada;
