import React from "react";
import Portada from "../Portada/Portada";
import { useParams } from "react-router-dom";
import { noticias } from "../../utils/noticias";
import "./Noticia.scss";
import { Container } from "react-bootstrap";

const Noticia = () => {
  const params = useParams();
  const idNoticia = params.idNoticia;
  const itemNoticia = noticias.find((i) => i.id === parseInt(idNoticia, 10));

  if (!itemNoticia) {
    return <div>No se encontró la noticia.</div>;
  }

  return (
    <div className="sec-noticia">
      <Portada />
      <Container className="notice">
        <h1 className="notice__title">{itemNoticia.title.toUpperCase()}</h1>

        {idNoticia === "1" && (
          <div className="notice__content">
            <div className="parrafo">
              <p>
                Así como muchas profesiones y oficios se debieron reinventar en
                esta nueva normalidad pandémica, la industria del inmueble no
                fue la excepción.
              </p>
              <p>
                En esta nueva manera de ofrecer al cliente un inmueble fue que
                surgió la necesidad de crear una plataforma casi tangible, para
                ofrecer la mejor experiencia a la hora de adquirir un nuevo
                inmueble. Antes de empezar es importante distinguir este dos
                tipos de realidades; Realidad Virtual y Realidad Aumentada, pues
                a pesar de que ambas son virtuales, difieren entre ellas en
                cuanto al tipo de percepción que recibimos y los medios en que
                los recibimos.
              </p>
              <p>
                <strong>Realidad Virtual:</strong> Representación de escenas o
                imágenes de objetos producida por un sistema informático, que da
                la sensación de su existencia real. (RAE).
              </p>
              <p>
                <strong>Realidad Aumentada:</strong>&nbsp;Conjunto de
                tecnologías que permiten que un usuario visualice parte del
                mundo real a través de un dispositivo tecnológico con
                información gráfica añadida por este dispositivo (Wikipedia)
              </p>
            </div>
            <div className="img-container d-flex justify-content-center">
              <img src="https://i.imgur.com/c16qzFI.png" alt="" />
            </div>
            <div className="parrafo">
              <p>
                Por lo tanto, con la Realidad Virtual (VR), experimentamos una
                realidad totalmente inmersiva, viviéndola en primera persona. La
                realidad virtual, necesita de un dispositivo de gafas VR para
                que podamos vivir dicha experiencia. Estas gafas pueden resultar
                muy sencillas, aportándonos simplemente un experiencia visual, o
                muy completas si se acompañan de accesorios para gestionar
                nuestros movimientos o nuestra capacidad auditiva.
              </p>
              <p>
                En lo que se refiere a la Realidad Aumentada (AR), no viviremos
                una experiencia completamente inmersiva, si no que aquellas
                imágenes generadas artificialmente se mostrarán en nuestro mundo
                real a través de un dispositivo como un móvil con cámara
                cualquiera.
              </p>
            </div>
            <div className="img-container d-flex justify-content-center">
              <img src="https://i.imgur.com/zY7SYWA.jpg" alt="" />
            </div>
            <div className="parrafo">
              <p>
                <strong>
                  Como aplicar estas tecnologías al sector inmobiliario
                </strong>
              </p>
              <p>
                El uso de estas tecnologías en el sector inmobiliario ha
                impactado con fuerza prácticamente en los primeros pasos de
                estas tecnologías. Es posible que hayamos podido experimentar
                dichos usos en algún caso. De todas formas me gustaría ofrecer
                algunos ejemplos.
              </p>
              <p>
                Uno de los primeros usos que se le dio a la realidad aumentada
                en el sector inmobiliario, es en la venta de promociones
                inmobiliarias. Las tradicionales maquetas de ejemplo de la
                promoción a construir y comercializar, quedarán atrás en
                detrimento de la Realidad Aumentada. Realizar dichas maquetas en
                AR es más barato y produce un mayor efecto, por lo menos hasta
                el momento. Además, permite diferentes modos de visionado, por
                ejemplo, podemos elegir ver solo ciertas plantas, o mostrar
                solamente puntos concretos del edificio.
              </p>
            </div>
            <div className="img-container d-flex justify-content-center">
              <img src="https://i.imgur.com/Opv9m4e.jpg" alt="" />
            </div>
            <div className="parrafo">
              <p>
                <strong>Diseño de construcción</strong>
              </p>
              <p>
                Como mencionamos anteriormente, la realidad virtual y aumentada
                se han utilizado en la industria de la construcción durante
                años. Sin embargo, cada vez más administradores de propiedades y
                asociaciones de propietarios se están dando cuenta de que estas
                herramientas también pueden ser útiles en proyectos de
                construcción de menor escala. Por ejemplo, antes de embarcarse
                en un gran proyecto de renovación, la realidad virtual y
                aumentada puede dar a los propietarios e inversores una idea de
                cómo será el resultado final. Las tecnologías de realidad
                virtual y aumentada se utilizan cada vez más para tomar
                importantes decisiones previas a la construcción.
              </p>
              <p>
                <strong>Seguimiento del progreso del proyecto</strong>
              </p>
              <p>
                Antes el dueño o administrador de una propiedad tenía que
                visitar un sitio en persona para tener una idea de cómo iba el
                proyecto de renovación. Gracias a la realidad virtual, ese ya no
                es el caso.
              </p>
            </div>
            <div className="img-container d-flex justify-content-center">
              <img src="https://i.imgur.com/ES9jbBJ.jpg" alt="" />
            </div>
            <div className="parrafos">
              <p>
                Propietarios, administradores de propiedades y asociaciones de
                propietarios pueden aprovechar la tecnología de realidad virtual
                para hacer visitas interactivas y virtuales de un proyecto en
                construcción. Esto puede mejorar la comunicación con los
                contratistas, asegurar que se cumplan los estándares de diseño y
                finalizar los proyectos.
              </p>
              <p>
                <strong>
                  ¿Cómo se utilizará la realidad virtual o la realidad
                  aumentada?
                </strong>
              </p>
              <p>
                La realidad virtual y la realidad aumentada son todavía bastante
                nuevas. Sin embargo, las aplicaciones de software están
                facilitando el uso de estas tecnologías con un dispositivo
                móvil. El sistema iOS 11 de Apple incluye una función ARKit que
                permite a los desarrolladores crear aplicaciones y experiencias
                de realidad aumentada de una manera más sencilla.
              </p>
              <p>
                Por ejemplo, Wayfair ha integrado funciones en su aplicación
                móvil en la que los usuarios pueden tener una vista previa del
                aspecto que tendrá todo en una habitación, desde sillas hasta
                lámparas de araña, y lo pueden hacer con solo pulsar un botón.
              </p>
              <p>
                A medida que los smartphones evolucionan, las tecnologías de
                realidad virtual Y realidad aumentada asociadas a ellos también
                lo hacen. Estas son noticias extraordinarias para los
                propietarios, administradores de propiedades y asociaciones de
                propietarios. A medida que la realidad virtual y aumentada se
                haga más asequible (y más fácil de usar), se convertirán en
                herramientas aún más importantes a la hora de agilizar las
                operaciones y procesos de administración de propiedades.
              </p>
            </div>
            <div className="img-container d-flex justify-content-center">
              <img src="https://i.imgur.com/GNMpn12.jpg" alt="" />
            </div>
            <div className="parrafos">
              <p>
                La pandemia por el nuevo coronavirus obligó a que algunas
                industrias cuyo desenvolvimiento dependía de una relación cara a
                cara se reconviertan. Un ejemplo de esto es el sector
                inmobiliario del Chaco el cual debió apelar a la creatividad y
                la tecnología para mantener una estrecha relación con los
                clientes y sus necesidades, pero a distancia.
              </p>
              <p>
                <strong>¿En qué consistió esta reinvención?&nbsp;</strong>
              </p>
              <p>
                Realidad aumentada, experiencia inmersiva e interactividad. Más
                claro aún: una desarrolladora chaqueña del sector inmobiliario
                utiliza tecnología PropTech la que permite realizar recorridos
                virtuales basados en la gamificación en los que la libertad de
                movimientos y la interactividad posibilitan al cliente conocer
                cada rincón de lo que será su próximo hogar con la opción de
                personalizarlo de manera íntegra: agregar, quitar o cambiar, por
                ejemplo, el color de las paredes, el estilo de los cerámicos,
                tipos de revestimientos, la cocina o cualquier otro aspecto de
                la vivienda.
              </p>
              <p>
                Esta innovadora tecnología aplicada a un recorrido virtual con
                realidad aumentada causa una sensación de identificación y
                permanencia con el proyecto ya que las personas logran
                visualizar su nuevo hogar con un mayor nivel de detalle que en
                con un render estático, donde estas posibilidades están más
                acotadas.
              </p>
              <p>
                El recorrido simulado no es un vídeo. Es una aplicación diseñada
                y programada para interactuar con el entorno dentro o fuera del
                departamento a construir, en un servicio que hasta ahora sólo se
                podía acceder en países como EEUU, España o Inglaterra.
              </p>
              <p>
                Actualmente, esta aplicación se está utilizando en lo que será
                el edificio “SF745 Casas en Altura”, que se construirá en
                Resistencia y cuyo desarrollo y construcción está a cargo de la
                firma BASA. El mismo ofrece una oportunidad óptima para
                brindarle al cliente una experiencia que de otra forma sería
                imposible.
              </p>
            </div>
            <div className="img-container d-flex justify-content-center">
              <img src="https://i.imgur.com/o7Gjuf1.jpg" alt="" />
            </div>
            <div className="parrafos">
              <p>
                “La pandemia y la cuarentena nos impulsó a la digitalización de
                nuestros servicios, y desde ese momento buscamos la posibilidad
                de implementar otra alternativa que vayan más allá de un render
                estático y que nos permita diferenciarnos y mejorar la
                experiencia con nuestros clientes.”, explicó Jorge Almirón,
                Gerente Comercial
              </p>
              <p>
                “Fue todo un desafío continuar con los proyectos relacionados al
                desarrollo inmobiliario, y con ella llego la imposibilidad de
                poder encontrarnos cara a cara con posibles clientes y
                ofrecerles nuestros nuevos proyectos”, contó, al tiempo que
                indicó que en este caso la aplicación muestra un semipiso de
                tres dormitorios con tres baños.
              </p>
              <p>
                <strong>
                  ¿Qué posibilidades de interacción tiene el recorrido virtual
                  “SF745 – Casas en Altura”?&nbsp;
                </strong>
              </p>
              <p>
                <strong>Con esta herramienta se puede:&nbsp;</strong>
              </p>
              <p>
                • Cambiar materiales de pisos, paredes, mesadas de la cocina,
                revestimiento en la cocina, mueble de alacena, mueble del bajo
                mesada, puertas de armarios, material del vanitory en el baño,
                mesada del vanitory, revestimiento del baño y revestimiento en
                la ducha.
              </p>
              <p>
                • Cambiar la vista que se ve desde el balcón según en qué piso
                te encuentres, la vista también cambiará según estés en el
                departamento del frente o del contra frente.
              </p>
              <p>
                • Recorrer el departamento en tres modos distintos: primera
                persona, aéreo y cámara.
              </p>
            </div>
            <div className="parrafos parrafos--fuente">
              <p>
                Fuente:{" "}
                <a
                  href="https://www.diarionorte.com/205042-interactividad-y-realidad-aumentada-asi-se-planifica-el-futuro-de-la-industria-inmobiliaria-en-el-chaco%C2%A0"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  https://www.diarionorte.com/205042-interactividad-y-realidad-aumentada-asi-se-planifica-el-futuro-de-la-industria-inmobiliaria-en-el-chaco%C2%A0
                </a>
              </p>
            </div>
          </div>
        )}
      </Container>
    </div>
  );
};

export default Noticia;
