import React from "react";
import Carousel from "react-bootstrap/Carousel";
import "./Hero.scss";
import { Button } from "react-bootstrap";

const images = [
  "https://i.imgur.com/P4u0B6v.jpg", // Más información
  "https://i.imgur.com/JgRizmZ.jpg", // Desarrollamos confianza
  "https://i.imgur.com/o2Nifnl.jpg", // +20 años de trayectoria
  "https://i.imgur.com/ZjkS9Fn.jpg", // Desarrollamos desde el momento 0
  "https://i.imgur.com/teu32PM.jpg", // Basa sello de calidad
];

const imagesMob = [
  "https://i.imgur.com/JgRizmZ.jpg", // Desarrollamos confianza
  "https://i.imgur.com/o2Nifnl.jpg", // +20 años de trayectoria
  "https://i.imgur.com/ZjkS9Fn.jpg", // Desarrollamos desde el momento 0
  "https://i.imgur.com/teu32PM.jpg", // Basa sello de calidad
];

const Hero = () => {
  let details = navigator.userAgent;
  let regexp = /android|iphone|kindle|ipad/i;
  let isMobileDevice = regexp.test(details);
  // const [imagesLoaded, setImagesLoaded] = useState(false);

  return (
    <div className="sec-hero">
      <Carousel className="car" id="inicio" interval={4500}>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[0] : images[0]}
            alt="First slide"
          />
          <Carousel.Caption className="car-item-caption">
            <a
              href="https://torresmagistrados.com"
              target="_blank"
              rel="noreferrer"
            >
              <Button variant="outline-light">Más información</Button>
            </a>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[1] : images[1]}
            alt="First slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>{"Desarrollamos Confianza"}</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[2] : images[2]}
            alt="Second slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>{"+20 años de trayectoria"}</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[3] : images[4]}
            alt="Tercer slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>{"Desarrollamos desde el momento 0"}</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[4] : images[4]}
            alt="Cuarto slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>{"Basa sello de Garantía"}</h3>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </div>
  );
};

export default Hero;
